export default {
    props: {
        items: {
            type: Array,
            default: []
        },
        default: { //清除选择
            type: Number,
            default: -1
        }
    },
    data() {
        return {
            current: 0
        }
    },
    watch: {
        default() {
            
            this.current=this.default;
        }
    },
    methods: {
        clickItem(index) {
            this.current = index;
            this.$emit('click', this.items[index])
        }
    },
}
